/**
 * 该文件只用于说明 mtl.debug.js 数据结构，不实际使用。
 */
define(function () {
  'use strict';

  const SUCCESS = 200;
  const FAIL = 0;

  return [
    {
      api: 'getNetworkType',
      result() {
        return {
          data: {
            networkType: 'wifi'
          }
        }
      }
    },
    {
      api: 'getLocation',
      result() {
        const condition = false;
        if (condition) {
          return {
            code: SUCCESS,
            data: {
              latitude: 45,
              longitude: 45
            }
          }
        }
        // 支持自定义的 code
        return {
          code: FAIL,
          message: 'Debug: error message'
        };
      }
    }
  ]
});